body {
    padding-top: 54px;
    padding-bottom: 54px;
    background: lighten($color: $light-shades, $amount: 1%); // $light-shades;
    font-family: 'Open Sans', sans-serif;
}

a {
    color: $main-brand;
    text-decoration: none;
    transition: all 0.3s;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
}

a:hover,
a:focus {
    color: $light-accent;
    text-decoration: none;
    transition: all 0.3s;
    font-weight: bold;
}

.bg-dark {
    background-color: $dark-accent !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Halant', serif;
//    color: $main-brand;
}

li.pubs {
    margin: 10px 0px;
}

.navbar {
//    padding: 15px 10px;
    background: $dark-accent !important;
    border: none;
    border-radius: 0;
    margin-bottom: 40px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

.navbar-btn {
    box-shadow: none;
    outline: none !important;
    border: none;
}

p {
    font-family: 'Open Sans', sans-serif;
    font-size: 1em;
    font-weight: 300;
    line-height: 1.5em;
    color: $dark-shades;
    text-align: justify;
}




